import React, { useRef } from 'react';
import { describe, PropTypes } from 'react-desc';
import { aggregatedClasses } from '@elliemae/ds-classnames';
import FocusGroupProvider from '@elliemae/ds-shared/FocusGroup/FocusGroupManager';
import { mergeRefs } from '@elliemae/ds-utilities/system';
import decorateToolbarChildren from './decorateToolbarChildren';
import Item from './ToolbarItem';
import ToolbarGroup from './ToolbarGroup';
import Divider from './ToolbarDivider';

const blockName = 'toolbar';

const ToolbarContainer = aggregatedClasses('div')(
  blockName,
  null,
  ({ withDepth, alignment, size }) => ({
    'without-depth': !withDepth,
    [alignment]: !!alignment,
    [size]: size,
  }),
);

const DSToolbar = ({
  containerProps = {},
  innerRef,
  withDepth = true,
  alignment = 'right', // left || right
  autoFocusOnMount = true,
  children = [],
  size = 'normal',
  ...otherProps
}) => {
  const containerRef = useRef(null);
  return (
    <FocusGroupProvider
      autoFocusOnMount={autoFocusOnMount}
      getContainer={() => containerRef.current}
      keyBindings={{
        Tab: 'next',
      }}
      orientation="horizontal"
    >
      <ToolbarContainer
        {...containerProps}
        {...otherProps}
        classProps={{ withDepth, alignment, size }}
        innerRef={mergeRefs(innerRef, containerRef)}
      >
        {decorateToolbarChildren(children)}
      </ToolbarContainer>
    </FocusGroupProvider>
  );
};

const toolbarProps = {
  containerProps: PropTypes.object.description(
    'Set of Properties attached to the main container',
  ),
  withDepth: PropTypes.bool
    .description('Shows a shadow rear the toolbar')
    .defaultValue(true),
  alignment: PropTypes.oneOf(['right', 'left'])
    .description('Aligns the toolbar to the left or right')
    .defaultValue('right'),
  size: PropTypes.oneOf(['normal', 'compact'])
    .description('Toolbar size')
    .defaultValue('normal'),
  autoFocusOnMount: PropTypes.bool
    .description('Wheter to focus the component when it mounts or not')
    .defaultValue(true),
  children: PropTypes.oneOfType([
    PropTypes.array,
    PropTypes.func,
    PropTypes.node,
  ]).description('Toolbar items').isRequired,
  innerRef: PropTypes.object.description(
    'Ref to the Toolbar container element',
  ),
};

DSToolbar.propTypes = toolbarProps;

const ToolbarWithSchema = describe(DSToolbar);
ToolbarWithSchema.propTypes = toolbarProps;

export {
  Item as ToolbarItem,
  ToolbarGroup,
  Divider as ToolbarDivider,
  ToolbarWithSchema,
};

export default DSToolbar;
